.footer {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    margin: 0 auto;
    padding: 15px 0;
    background-color: #f7f7f7;
  }
  
  .footer__section {
    display: flex;
    flex-direction: column;
  
    width: 100%;
  
    @media screen and (min-width: 768px) {
      height: 77px;
      flex-direction: row;
      justify-content: space-around;
      align-items: center;
    }
  
    @media screen and (min-width: 768px) {
    }
  }
  
  .footer__copyright {
    margin-bottom: 15px;
  
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 1.14;
  
    @media screen and (max-width: 767px) {
      order: 1;
    }
  
    @media screen and (min-width: 768px) {
      margin: 0;
      font-size: 18px;
      line-height: 1.187;
    }
  
    @media screen and (min-width: 1280px) {
      flex-direction: row;
    }
  }
  
  .footer__text {
    margin: 0 0 10px 0;
  
    @media screen and (min-width: 1280px) {
      margin: 0 5px 0 0;
    }
  }
  
  .footer__heart {
    margin: 0 5px;
  }
  
  .footer__link {
    font-weight: 500;
    text-decoration: underline;
    cursor: pointer;
  
    transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1);
  
    &:hover,
    &:focus {
      color: #ff6b08;
    }
  }
  
  @media screen and (max-width: 767px) {
    .social {
      margin-bottom: 15px;
    }
  }
  
  .social,
  .app {
    text-align: center;
  
    @media screen and (max-width: 767px) {
      order: 2;
    }
  }
  
  .social__text,
  .app__text {
    margin-bottom: 10px;
  
    text-transform: uppercase;
    font-size: 16px;
    font-weight: 500;
  
    @media screen and (min-width: 768px) {
      margin-bottom: 15px;
      font-size: 19px;
    }
  }
  
  .social__list,
  .app__list {
    display: flex;
    justify-content: center;
    align-items: center;
  
    @media screen and (max-width: 767px) {
      display: flex;
      justify-content: center;
      align-items: center;
    }
  }
  
  .social__item:not(:last-child) {
    margin-right: 15px;
  }
  
  .app__item:not(:last-child) {
    margin-right: 10px;
  }
  
  .social__link,
  .app__link {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 30px;
    height: 30px;
  
    color: black;
    transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1);
  
    &:hover,
    &:focus {
      color: #ff6b08;
    }
  }
  
  .social__icon,
  .app__icon {
    fill: currentColor;
  }