.header {
  height: 230px;
  max-width: 1600px;
  margin: 0 auto;
  background-size: cover;
  background-position: 50% 50%;
  background-repeat: no-repeat;
}

.home-header-bg {
  background-image: linear-gradient(
      to right,
      rgba(#000000, 0.56),
      rgba(#000000, 0.56)
    ),
    url(../images/header/header-bg/home-header-bg-mobile.jpg);

  @media (min-device-pixel-ratio: 2),
    (min-resolution: 192dpi),
    (min-resolution: 2dppx) {
    background-image: linear-gradient(
        to right,
        rgba(#000000, 0.56),
        rgba(#000000, 0.56)
      ),
      url(../images/header/header-bg/home-header-bg-mobile@2x.jpg);
  }
}

.library-header-bg {
  background-image: linear-gradient(
      to right,
      rgba(#000000, 0.56),
      rgba(#000000, 0.56)
    ),
    url(../images/header/header-bg/library-header-bg-mobile.jpg);

  @media (min-device-pixel-ratio: 2),
    (min-resolution: 192dpi),
    (min-resolution: 2dppx) {
    background-image: linear-gradient(
        to right,
        rgba(#000000, 0.56),
        rgba(#000000, 0.56)
      ),
      url(../images/header/header-bg/library-header-bg-mobile@2x.jpg);
  }
}

.header-container {
  padding-top: 40px;
}

.naw-wrapper {
  display: flex;
  justify-content: space-between;
  margin-bottom: 54px;
}

.naw-list {
  display: flex;
}

.naw-list-item:not(:last-child) {
  margin-right: 40px;
}
.naw-list-link {
  text-transform: uppercase;
  color: white;
  cursor: pointer;
}

.current::after {
  display: block;
  content: '';
  width: 100%;
  height: 3px;
  background-color: red;
}

.search-form {
  margin-top: 54px;
  position: relative;
}

.search-form__input {
  display: block;
  width: 100%;
  background-color: transparent;
  border: none;
  padding-bottom: 4px;
  border-bottom: 0.5px solid white;
  color: white;
  outline: none;
  &:focus {
    border-color: red;
    border-width: 1px;
    fill: red;
  }
}

.search-icon {
  position: absolute;
  top: 0;
  right: 0;
  transform: translateY(50%);
}

.header-btn-list {
  display: flex;
  justify-content: center;
  gap: 20px;
  margin-top: 60px;
}

.header-btn {
  text-transform: uppercase;
  display: block;
  text-align: center;
  width: calc((100% - 30px) / 2);
  border: 1px solid white;
  padding-bottom: 12px;
  padding-top: 14px;
  border-radius: 5px;
  color: white;
  cursor: pointer;
}

.header-btn:focus {
  background-color: #ff6b01;

  border-color: transparent;
  color: white;
}

