@include breakpoints(tablet) {
  .header {
    height: 216px;
  }

  .header-container {
    padding-top: 45px;
  }

  .home-header-bg {
    background-image: linear-gradient(
        to right,
        rgba(#000000, 0.56),
        rgba(#000000, 0.56)
      ),
      url(../images/header/header-bg/home-header-bg-tablet.jpg);
    background-size: cover;
    background-position: 50% 50%;
    background-repeat: no-repeat;

    @media (min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      background-image: linear-gradient(
          to right,
          rgba(#000000, 0.56),
          rgba(#000000, 0.56)
        ),
        url(../images/header/header-bg/home-header-bg-tablet@2x.jpg);
    }
  }

  .library-header-bg {
    background-image: linear-gradient(
        to right,
        rgba(#000000, 0.56),
        rgba(#000000, 0.56)
      ),
      url(../images/header/header-bg/library-header-bg-tablet.jpg);

    @media (min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      background-image: linear-gradient(
          to right,
          rgba(#000000, 0.56),
          rgba(#000000, 0.56)
        ),
        url(../images/header/header-bg/library-header-bg-tablet@2x.jpg);
    }
  }

  .search-form {
    width: 336px;
    margin: 0 auto;
  }

  .header-btn-list {
    gap: 32px;
  }

  .header-btn {
    width: 150px;
  }
}

@include breakpoints(desktop) {
  .home-header-bg {
    background-image: linear-gradient(
        to right,
        rgba(#000000, 0.56),
        rgba(#000000, 0.56)
      ),
      url(../images/header/header-bg/home-header-bg-desktop.jpg);
    background-size: cover;
    background-position: 50% 50%;
    background-repeat: no-repeat;

    @media (min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      background-image: linear-gradient(
          to right,
          rgba(#000000, 0.56),
          rgba(#000000, 0.56)
        ),
        url(../images/header/header-bg/home-header-bg-desktop@2x.jpg);
    }
  }
  .library-header-bg {
    background-image: linear-gradient(
        to right,
        rgba(#000000, 0.56),
        rgba(#000000, 0.56)
      ),
      url(../images/header/header-bg/library-header-bg-desktop.jpg);

    @media (min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      background-image: linear-gradient(
          to right,
          rgba(#000000, 0.56),
          rgba(#000000, 0.56)
        ),
        url(../images/header/header-bg/library-header-bg-desktop@2x.jpg);
    }
  }

  .search-form {
    width: 336px;
    margin: 0 auto;
  }
}
